@import url(https://api.tiles.mapbox.com/mapbox-gl-js/v1.4.1/mapbox-gl.css);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.legend {
  margin: 20px 0; }

.map {
  margin: 20px auto;
  width: 960px; }

input {
  padding: 1em;
  border: 1px solid black;
  border-radius: 3px; }

body {
  font-family: 'Roboto', sans-serif; }
  body section {
    border: 1px solid grey;
    padding: 2rem; }
    body section p {
      margin: 0;
      font-size: 2rem;
      text-align: right;
      display: none; }
    body section h1 {
      margin: 0;
      padding: 0 0 1rem 0;
      text-align: center;
      font-size: 3rem; }
    body section form {
      display: flex;
      flex-direction: column; }
      body section form div {
        margin: 0 0 1rem 0;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center; }
        body section form div label {
          margin-right: 1rem;
          width: 25vw;
          font-weight: bold;
          text-align: start; }
        body section form div input, body section form div select, body section form div textarea {
          outline: 1px solid black;
          width: 70vw;
          padding: .5rem;
          font-size: 1rem; }
        body section form div textarea {
          height: 6rem; }
      body section form button {
        background-color: black;
        color: white;
        padding: 1rem;
        font-size: 1.5rem;
        margin-top: 2rem; }

